@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap');

:root {
  --soft-red: hsl(10, 79%, 65%);
  --cyan: hsl(186, 34%, 60%);
  --darkbrown: hsl(25, 47%, 15%);
  --mediumbrown: hsl(28, 10%, 53%);
  --cream: hsl(27, 66%, 92%);
  --verypaleorange: hsl(33, 100%, 98%);
}

/* Global resets */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'DM Sans', sans-serif;
  min-height: 100vh;
  background-color: var(--cream);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

/* Main container */
main {
  width: 350px;
}

canvas {
  width: 100%;
  max-width: 300px;
  height: 400px;
  margin: 30px;
}

/* Top section */
.top {
  background-color: var(--soft-red);
  color: white;
  border-radius: 1rem;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
}

/* Balance section inside the top */
.balance {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.balance p {
  font-weight: 400;
}

/* Bottom section */
.bottom {
  background-color: white;
  border-radius: 1rem;
  margin-top: 1rem;
  padding: 1rem;
}

.bottom h1 {
  color: var(--darkbrown);
}

#myChart {
  margin: 1rem 0;
}

/* Line */
.line {
  background-color: var(--cream);
  height: 2px;
  margin-bottom: 1rem;
}

/* Totals section */
.totals {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.totals h2 {
  color: var(--darkbrown);
}

.totals p {
  color: var(--mediumbrown);
}

/* Last month section */
.last_month p:first-child {
  font-weight: 700;
  color: var(--darkbrown);
}

.last_month p:last-child {
  color: var(--mediumbrown);
}
